  vertical-align: baseline;
}

sup {
  top: -.5em;
}

sub {
  bottom: -.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em;
}

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button, input {
  line-height: normal;
}

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled], input[disabled] {
  cursor: default;
}

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body, figure {
  margin: 0;
}

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0;
}

* {
  box-sizing: border-box;
}

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden;
}

/*VARIABLES*/
/*FONT*/
/*MAIN STYLING*/
html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #171c20;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

header {
  min-height: 10vh;
  background-color: #171c20;
  border-bottom: 3px solid #d0a360;
}

main {
  min-height: 80vh;
}

footer {
  min-height: 4vh;
}

h1 {
  font-family: "Libre Caslon Display", serif;
  font-size: 7rem;
  color: #d0a360;
  text-align: center;
  margin: 0;
  letter-spacing: -3px;
}

h2 {
  font-family: "Libre Caslon Display", serif;
  font-size: 3.2rem;
  border-bottom: 5px solid #d0a360;
  margin: 0;
}

h3 {
  font-family: "Libre Caslon Display", serif;
  font-size: 4rem;
  margin: 0;
}

p {
  font-size: 2.3rem;
  color: #171c20;
  padding: 10px;
  margin: 0;
  margin-bottom: 5px;
}

ul,
li {
  list-style: none;
}

button {
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%);
  border: none;
  margin: 0;
  background: #2c363e;
  box-shadow: 4px 4px #d0a360;
  color: white;
  padding: 15px 15px;
  font-size: 2rem;
  border-radius: 25px;
  outline: none;
  overflow: hidden;
  -webkit-transition: 500ms ease all;
  transition: 500ms ease all;
}

button:before {
  content: '';
  position: absolute;
  top: -40%;
  right: 110%;
  width: 30px;
  height: 200%;
  background: rgba(255, 255, 255, 0.3);
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
}

button:hover {
  background: #dfc193;
  box-shadow: 2px 2px #171c20;
}

button:hover:before {
  right: -50%;
  -webkit-transition: 1s ease all;
  transition: 1s ease all;
}

button:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* Skip-line for accessibility */
.skipLink {
  position: absolute;
  left: -1000px;
  top: 5px;
  z-index: 999;
  background: transparent;
  color: transparent;
}

.skipLink:focus {
  left: 0;
}

/* Global Styles End Here */
