/*MAIN SECTION - GRID SETUP*/
.gridContainer {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  -ms-grid-rows: (min-content)[4];
      grid-template-rows: repeat(4, -webkit-min-content);
      grid-template-rows: repeat(4, min-content);
  grid-gap: 5px;
  gap: 5px;
      grid-template-areas: "introduction introduction introduction" "got heart hp" "message message message" "outing outing outing";
}

.outing {
  -ms-grid-row: 4;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: outing;
  background-color: #171c20;
  overflow: hidden;
}

.friendshipImage {
  width: 100px;
  height: 100px;
}

.app {
  background-image: url(/GameOfPotter/static/media/backgroundPattern.f9500e7a.jpg);
  background-size: contain;
  background-attachment: fixed;
}

.gotCharacters {
  -ms-grid-row: 2;
  -ms-grid-column: 1;
  grid-area: got;
  margin: 0 auto;
  text-align: center;
}

.hpCharacters {
  -ms-grid-row: 2;
  -ms-grid-column: 3;
  grid-area: hp;
  margin: 0 auto;
  text-align: center;
}

.gotImages,
.hpImages {
  width: 290px;
  height: 290px;
  border-radius: 50%;
  border: 15px solid #d0a360;
}

/*HEART SECTION*/
.affinyWrapper {
  -ms-grid-row: 2;
  -ms-grid-column: 2;
  grid-area: heart;
}

.heartIcon {
  width: 400px;
  height: 400px;
  margin: 0 auto;
}

.heartWrapper {
  position: relative;
  align-self: center;
  display: flex;
}

.heartWrapper h2 {
  position: absolute;
  top: 37%;
  left: 39%;
  border-bottom: none;
  font-size: 5rem;
  color: #e4e4df;
  text-shadow: 3px 3px #171c20;
}

.message {
  -ms-grid-row: 3;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: message;
  text-align: center;
}

.message h3 {
  font-size: 3rem;
}

.introduction {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: introduction;
  text-align: center;
  width: 70%;
  margin: 0 auto;
  font-size: 2rem;
}

.introduction p {
  padding: 0;
  font-size: 1.9rem;
}

.introduction span {
  font-weight: 900;
}

.outing {
  border-top: 2px solid #d0a360;
}

.outing .title {
  font-size: 5rem;
  text-align: center;
  padding: 20px;
  color: #d0a360;
  border: none;
}

.outing .outingSuggestions ul {
  display: flex;
  flex-wrap: wrap;
  color: #e4e4df;
  margin: 0;
  padding: 0;
}

.outing .outingSuggestions ul .restaurantImg {
  border-radius: 50%;
  border: 5px solid #d0a360;
}

.outing .outingSuggestions ul li {
  display: flex;
  width: calc(100% / 3);
  height: 240px;
  margin: 0;
  padding: 1%;
}

.outing .outingSuggestions ul li .restaurantInfo {
  margin: 5% 0;
  width: 100%;
}

.outing .outingSuggestions ul li .restaurantInfo h3,
.outing .outingSuggestions ul li .restaurantInfo h4,
.outing .outingSuggestions ul li .restaurantInfo h5 {
  margin: 5%;
  margin-left: 5%;
}

.outing .outingSuggestions ul li .restaurantInfo h3 {
  font-size: 2.6rem;
}

.outing .outingSuggestions ul li .restaurantInfo h4 {
  font-size: 1.7rem;
}

.outing .outingSuggestions ul li .restaurantInfo h5 {
  font-size: 1.5rem;
}

/*FOOTER SECTION*/
footer {
  font-size: 1.5rem;
  text-align: center;
  color: #e4e4df;
  background-color: #171c20;
  border-top: 2px solid #d0a360;
}

footer span {
  color: #d0a360;
}

@media (max-width: 1220px) {
  .hpImages,
  .gotImages {
    width: 300px;
    height: 300px;
  }
  button {
    padding: 10px;
  }
  .individualGotCharacter,
  .individualHpCharacter {
    min-height: 350px;
  }
}

@media (max-width: 1050px) {
  .heartIcon {
    width: 300px;
    height: 300px;
  }
  .outing .outingSuggestions ul li {
    display: block;
    height: auto;
  }
  .outing .outingSuggestions ul li img {
    margin-left: 20%;
  }
}

@media (max-width: 915px) {
  .introduction {
    width: 80%;
  }
  .hpImages,
  .gotImages {
    width: 250px;
    height: 250px;
  }
  button {
    font-size: 1.5rem;
  }
  .message h3 {
    font-size: 3rem;
  }
  .outing .outingSuggestions ul li img {
    margin-left: 15%;
  }
}

@media (max-width: 810px) {
  .hpImages,
  .gotImages {
    width: 220px;
    height: 220px;
  }
  .outing .outingSuggestions ul li img {
    margin-left: 10%;
  }
}

@media (max-width: 768px) {
  h2 {
    font-size: 3rem;
  }
  main {
    min-height: 60vh;
  }
  .heartWrapper h2 {
    left: 35%;
  }
  .outing .outingSuggestions ul li img {
    margin-left: 0;
  }
  .heartIcon {
    width: 200px;
    height: 200px;
  }
  .heartWrapper h2 {
    font-size: 3rem;
    left: 40%;
  }
  .message h3 {
    font-size: 2.5rem;
  }
  .introduction p {
    font-size: 1.7rem;
  }
}

@media (max-width: 650px) {
  .gridContainer {
    margin: 0 auto;
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
    -ms-grid-rows: (min-content)[6];
        grid-template-rows: repeat(6, -webkit-min-content);
        grid-template-rows: repeat(6, min-content);
    grid-gap: 5px;
    gap: 5px;
        grid-template-areas: "introduction" "got" "heart" "hp" "message" "outing";
  }
  button {
    display: block;
  }
  .heartWrapper h2 {
    left: 45%;
  }
  .outing .title {
    font-size: 4rem;
  }
  .outing .outingSuggestions {
    display: flex;
    justify-content: center;
  }
  .outing .outingSuggestions ul {
    display: block;
  }
  .outing .outingSuggestions ul li {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .outing .outingSuggestions ul li .restaurantInfo {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
}

@media (max-width: 375px) {
  header {
    min-height: 7vh;
  }
  header h1 {
    font-size: 5rem;
  }
  .outing .title {
    font-size: 3rem;
  }
  footer {
    min-height: 2vh;
    font-size: 1.2rem;
  }
}

